import React, { useState } from "react";
import Dialog from '../dialog/dialog.component';
import TextDialog from '../textInput-dialog/textInput-dialog.component';
const hamsanGozini_values = ['        ', '    '];

const isargari_values = ['', '','', '', '',];
const education_values = ['', '', ' ', '', ' ', ''];
const state_values = [
    '', '', ' ', '', '',
    '', ' ', '', '', '',
    '', '', '', '', '',
    ' ', '', '', '', '',
    '', '', '', '  ', '',
    '  ', '', '  ', ' ', ' ',
    ''];
const Form = ({disabled}) => {
    const [studentNumber, setStudentNumber] = useState('9442365104');
    const [hamsan, setHamsan] = useState('');
    const [collegeCity, setCollegeCity] = useState('');
    const [placeofIssue, setPlaceofIssue] = useState('');
    const [isargari, setIsargari] = useState('');
    const [education, setEducation] = useState('');
    const [enterYear, setEnterYear] = useState('68');

    return (


        <div style={{ direction: 'rtl' }}>


            <TextDialog state={studentNumber} setState={setStudentNumber} items={[]} title={' '} disabled={disabled} />
            <Dialog state={hamsan} setState={setHamsan} items={hamsanGozini_values} title={' '} disabled={disabled} />
            <Dialog state={collegeCity} setState={setCollegeCity} items={state_values} title={'  '} disabled={disabled} />
            <Dialog state={education} setState={setEducation} items={education_values} title={' '} disabled={disabled} />
            <TextDialog state={enterYear} setState={setEnterYear} items={[]} title={' '} disabled={disabled} />
            <TextDialog state={placeofIssue} setState={setPlaceofIssue} items={[]} title={' '} disabled={disabled} />
            <Dialog state={isargari} setState={setIsargari} items={isargari_values} title={' '} disabled={disabled} />
            





        </div>

    )
}


export default Form;







